import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {Pdf,ServicioService}from './../../../services/servicio.service';
@Component({
  selector: 'app-virtual',
  templateUrl: './virtual.page.html',
  styleUrls: ['./virtual.page.scss'],
})
export class VirtualPage implements OnInit {
  section="";
  show= false;
  textV="";textC="";textR="";textP="";
  aprendizaje;
  R; C; P;V = false;
  objaux:Pdf;
  pdf:Pdf={
    id :'',
    num:'',
    nombre:'',
    ambiente:'',
    factor:'',
    personaje:'',
    solitario:true,
    solido:'',
    gaseoso:'',
    liquido:'',
    P1:'',
    P2:'',
    P3_1:'',
    P3_2:'',
    P3_3:'',
    P3_4:'',
    tarjeta:'',
    tarjeta2:'',
    idea1:'',
    idea2:'',
    idea3:'',
    idea4:'',
    ideaselecionada:'',
    aprendizaje:'',
    V:'',
    C:'',
    R:'',
    P:'',
    tipo:'',
    img:''
  };
  avanzar(section){
    this.section = section;
  }
  constructor(private serv:ServicioService,private router:Router) { }
  async ngOnInit() {
    this.section="video";
  this.mipromesa();
  }
  iniciador(){
    this.pdf=this.objaux;
    this.pdf.tipo="virtual";
  }
  openPopUP(item){
    this.show = !this.show;
    switch (item) {           
      case 'V':
      this.V = !this.V;
        break;
        case 'R':
      this.R = !this.R;
          break;
          case 'C':
      this.C = !this.C;
          break;
    case 'P':
      this.P = !this.P;
          break;
      default:
        break;
    }
    this.pdf.V=this.textV;
    this.pdf.C=this.textC;
    this.pdf.P=this.textP;
    this.pdf.R=this.textR;
  }
  learn(){
    this.pdf.tipo="virtual";
    this.serv.addItem(this.pdf);
    this.serv.editar2(this.pdf.id,this.pdf,this.pdf.num);
  }
  alLector(){
    this.router.navigateByUrl('scanner');
  }
  maqueta(){
    this.router.navigateByUrl('mvirtual');
  }
  async mipromesa(){
    new Promise((resolve) => {
      this.objaux = JSON.parse(this.serv.getItem('_cap_pdf2'));
       this.pdf=  this.objaux; 
       return resolve(this.pdf);
   });
 }
 async mipromesa2(){
  new Promise((resolve) => {
    this.objaux = JSON.parse(this.serv.getItem('pdf'));
     this.pdf=  this.objaux; 
     return resolve(this.pdf);
 });
  }
}
